@mixin fontFace($family, $src, $weight: normal, $style: normal) {
  @font-face {
    font-family: $family;
    src: url('fonts/#{$src}.woff2') format('woff2'),
    url('fonts/#{$src}.woff') format('woff');

    font-style: $style;
    font-weight: $weight;
  }
}

@import url("https://use.typekit.net/cal2bhk.css");

@include fontFace('Replica', 'lineto-replica-pro-regular');
@include fontFace('Replica', 'lineto-replica-pro-italic', normal, italic);
@include fontFace('Replica', 'lineto-replica-pro-bold', bold);
@include fontFace('Replica', 'lineto-replica-pro-bolditalic', bold, italic);
@include fontFace('Replica', 'lineto-replica-pro-light', 300);
@include fontFace('Replica', 'lineto-replica-pro-lightitalic', 300, italic);
